@media (max-width: 1024px) {
    .cart-item {
        
        .cart-item_name {
            min-width: 120px;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
        }
    }
}

@media (max-width: 547px) {
    .cart-item_name {
        display: none;
    }

    .cart-item_amout {
        padding: 0 4px!important;
    }

    .cart-item_price {
        width: auto !important;
    }
}