.cart-wrap {
    --width-left: 100%;
    --width-right: 100%;

    position: fixed;
    top: 15%;
    left: 24px;
    right: 24px; 
    bottom: 15%;
    // padding: 24px;
    background-color: var(--main-bg);
    box-shadow: var(--shadow-open);
    color: var(--text-color-bold);
    border-radius: 16px;
    z-index: 1001;
    transform: translateY(calc(100% + 30%));
    transition: all 0.3s ease;
    overflow-y: scroll;

    &::-webkit-scrollbar {
        width: 0;
    }

    &.appearance {
        transform: translateY(0%);
    }

    .cart-wrap_left {
        width: var(--width-left);
    }

    .cart-wrap_right {
        width: var(--width-right);
    }

    .cart-wrap_list {
        overflow-y: auto;

        &::-webkit-scrollbar {
            width: 0;
        }
    }
}


@media (max-width: 547px) {
    .cart-wrap {
        left: 12px;
        right: 12px;
    }
}

@media (max-width: 427px) {
    .cart-wrap {
        left: 8px;
        right: 8px;
    }
}

@media (min-width: 768px) {
    .cart-wrap {
        left: 15%;
        right: 15%; 

        
    }
}

@media (min-width: 868px) {
    .cart-wrap {
        left: 18%;
        right: 18%; 
    }
}

@media (min-width: 968px) {
    .cart-wrap {
        left: 22%;
        right: 22%; 
    }
}

@media (min-width: 1024px) {
    .cart-wrap {
        --width-left: 68%;
        --width-right: 32%;

        left: 6%;
        right: 6%;

        .cart-wrap_left {
            border-top-left-radius: 16px;
            border-bottom-left-radius: 16px;
        }
    
        .cart-wrap_right {
            border-top-right-radius: 16px;
            border-bottom-right-radius: 16px;
        }
    }
}

@media (min-width: 1124px) {
    .cart-wrap {
        left: 8%;
        right: 8%;
    }
}

@media (min-width: 1280px) {
    .cart-wrap {
        left: 12%;
        right: 12%;
    }
}

@media (min-width: 1324px) {
    .cart-wrap {
        left: 15%;
        right: 15%;
    }
}

@media (min-width: 1534px) {
    .cart-wrap {
        left: 20%;
        right: 20%;
    }
}